import React, { Component } from "react";
import { Link } from "react-router-dom";
import { InputGroup, Input } from "react-bootstrap";
import HomeCartView from "../HomeCartView";
import MobileMenu from "../MobileMenu";
import device, { size } from "../../modules/mediaQuery";
import MediaQuery from "react-responsive";
import logo from "../../assets/images/LOGO.png";
import Auth from "../../modules/Auth";
import LoginRegister from "../LoginRegisterModal";
import { AiOutlineShoppingCart, AiOutlineHeart, AiOutlineUser } from "react-icons/ai";
import { Tooltip, UncontrolledTooltip } from "reactstrap";
import jumpTo from "../../modules/Navigation";

class NavBar extends Component {
  constructor(props) {
    super(props);
    this.state = {
      modalShow: false,
      activeclass: false,
      login: true,
      isTooltipOpen: false,
    };
  }

  componentDidMount() {
    if (Object.keys(this.props.cart).length < 1) {
      this.props.getCartByUserId();
    }
  }

  onToggleTooltip = () => {
    const { isTooltipOpen } = this.state;
    this.setState({ isTooltipOpen: !isTooltipOpen });
  };

  showHideModal = () => {
    this.setState({ modalShow: !this.state.modalShow });
  };


  handleMenuClicked = () => {
    this.setState({ activeclass: !this.state.activeclass });
  };

  loginClicked = () => {
    this.setState({ modalShow: true, login: true });
  };
  registerClicked = () => {
    this.setState({ modalShow: true, login: false });
  };



  logout = () => {
    Auth.logout();
    window.location.reload();
  };

  render() {
    const { departments, cart } = this.props;
    const { isTooltipOpen } = this.state;
    return (
      <div className="main_nav_container">
        <div className="container">
          <div className="row">
            <div className="col-lg-12 text-right">
              <div className="logo_container">
                <Link to="/fashion-cube">
                  <img src={logo} />
                </Link>
              </div>
              <nav className="navbar">
                <ul className="navbar_menu">
                  <li>
                    <Link to="/home">Auctions</Link>
                  </li>
                  <li className="mega-drop-down"> 
                    <a href="#" onClick={() => jumpTo('/fashion-cube/shops/')}>
                      Buy now it! 
                      {/* <i className="fa fa-angle-down"></i> */}
                    </a>

                    {/* <div className="mega-menu">
                      <div className="mega-menu-wrap">
                        {departments &&
                          departments.map((item, index) => {
                            return (
                              <div className="mega-menu-content" key={index}>
                                <h5>{item.departmentName}</h5>
                                <ul className="stander">
                                  {item.categories.split(",").map((i, idx) => {
                                    return (
                                      <li key={idx}>
                                        <a
                                          href={`/fashion-cube/shops/${item.departmentName}/${i}`}
                                        >
                                          {i}
                                        </a>
                                      </li>
                                    );
                                  })}
                                </ul>
                              </div>
                            );
                          })}
                      </div>
                    </div> */}
                  </li>
                  <li>
                    {/* <a href="contact.html">Buyers info</a> */}
                    <Link to="/buyerinfo">Buyers info</Link>
                  </li>
                  <li >
                    <a href="#" id="controlledTooltip">Open hours</a>

                  </li>
                  <Tooltip
                    delay={{ show: 1000, hide: 0 }}
                    isOpen={isTooltipOpen}
                    placement="bottom"
                    target="controlledTooltip"
                    toggle={this.onToggleTooltip}
                 
                    
                  // onClick={() => {}}
                  >
                    Monday, Tuesday, Wednesday, Friday, Saturday: 10:00 aM-7:00 PM | Sunday 11:00 AM-5:00 PM | Thursday closed 
                  </Tooltip>
                </ul>
                <ul className="navbar_user">
                  <li>
                    <div className="input-group " >
                      <input type="search" class="form-control " placeholder="Search " aria-label="Search" aria-describedby="search-addon" />
                      <span class="input-group-text " id="search-addon">
                        <i class="fas fa-search" style={{ color: "red" }}></i>
                      </span>
                    </div>

                  </li>
                  <li className="checkout" style={{ marginLeft: "20px" }}>

                    <a href="#" onClick={() => this.showHideModal()}>
                      <AiOutlineShoppingCart />
                      {cart.totalQty !== undefined && (
                        <span id="checkout_items" className="checkout_items">
                          {cart.totalQty}
                        </span>
                      )}
                    </a>
                  </li>

                  <li className="checkout" style={{ marginLeft: "20px" }}>

                    <a href="#">
                      <AiOutlineHeart />
                    </a>
                  </li>
                  {Auth.getUserDetails() !== undefined &&
                    Auth.getUserDetails() !== null &&
                    Auth.getToken() !== undefined ? (
                    <li className="account checkout" style={{ marginLeft: "20px", marginTop: "-7px" }}>
                      <a href="#">
                        {/* {`Welcome ${Auth.getUserDetails().user_name}`} */}
                        <AiOutlineUser size={20} />
                      </a>
                      <ul className="account_selection">
                        <li>
                          <a href="#" onClick={() => this.logout()}>
                            <i
                              className="fa fa-sign-in-alt"
                              aria-hidden="true"
                            ></i>
                            Logout
                          </a>
                        </li>
                      </ul>
                    </li>
                  ) : (
                    <li className="account">
                      <a href="#">
                        <i className="fa fa-user fa-lg" aria-hidden="true"></i>
                      </a>
                      <ul className="account_selection">
                        <li>
                          <a href="#" onClick={() => jumpTo('/login')}>
                            <i
                              className="fas fa-sign-in-alt"
                              aria-hidden="true"
                            ></i>
                            Sign In
                          </a>
                        </li>
                        <li>
                          <a href="#" onClick={() => this.registerClicked()}>
                            <i
                              className="fa fa-user-plus"
                              aria-hidden="true"
                            ></i>
                            Register
                          </a>
                        </li>
                      </ul>
                    </li>
                  )}
                </ul>
                <div
                  className="hamburger_container"
                  onClick={() => this.handleMenuClicked()}
                >
                  <i className="fa fa-bars" aria-hidden="true"></i>
                </div>
              </nav>
            </div>
          </div>
        </div>
        <MediaQuery query={device.max.tabletL}>
          <MobileMenu
            activeClass={this.state.activeclass}
            onClose={() => this.handleMenuClicked()}
          />
        </MediaQuery>
        {this.state.modalShow ? (
          <HomeCartView
            cart={cart}
            show={this.state.modalShow}
            onHide={() => this.showHideModal()}
          />
        ) : null}
        {this.state.modalShow ? (
          <LoginRegister
            show={this.state.modalShow}
            login={this.state.login}
            registerClicked={() => this.registerClicked()}
            loginClicked={() => this.loginClicked()}
            onHide={() => this.showHideModal()}
          />
        ) : null}
      </div>
    );
  }
}

export default NavBar;
