import React, { Component } from "react";
import { Redirect } from "react-router-dom";

// Layout Types
import BaseLayout from "../layouts/BaseLayout";

// Route Views
import Home from "../views/Home/HomeContainer";
import SingleProductContainer from "../views/Product/SingleProductContainer";
import CategoryContainer from "../views/Category/CategoryContainer";
import AboutContainer from "../views/About/About"
import BuyerInfoContainer from "../views/BuyerInfo/Buyerinfo";
import FAQsContainer from "../views/FAQs/FAQs";
import Termscondition from "../views/Termscondition/Termscondition";
import Refundpolicy from "../views/Refundpolicy/Refundpolicy";
import Privacypolcy from "../views/Privacypolcy/Privacypolcy";
import ContactContainer from "../views/Contact/Contack";
import BidProductContainer from "../views/Product/BidProductContainer";
import CheckOutContainer from "../views/CheckOut/CheckOut";
import BlogContainer from "../views/Blog/BlogContainer";
import BlogdetailsContainer from "../views/Blog/BlogdetailsContainer";
import Login from "../views/Login/Login";
import Register from "../views/Register/Register";

var routes = [
  {
    path: "/login",
    layout: BaseLayout,
    component: Login,
  },
  {
    path: "/register",
    layout: BaseLayout,
    component: Register,
  },
  {
    path: "/fashion-cube",
    exact: true,
    layout: BaseLayout,
    component: Home,
  },

  {
    path: "/home",
    layout: BaseLayout,
    component: () => <Redirect to="/fashion-cube" />,
  },
  {
    path: "/fashion-cube/single-product/:id",
    layout: BaseLayout,
    component: SingleProductContainer,
  },
  {
    path: "/fashion-cube/bid-product/:id",
    layout: BaseLayout,
    component: BidProductContainer,
  },
  {
    path: "/fashion-cube/shops",
    layout: BaseLayout,
    component: CategoryContainer,
  },
  {
    path: "/about",
    layout:BaseLayout,
    component:AboutContainer,
  },
  {
    path: "/buyerinfo",
    layout:BaseLayout,
    component:BuyerInfoContainer
  },
  {
    path: "/faqs",
    layout:BaseLayout,
    component:FAQsContainer
  },
  {
    path: "/termcondition",
    layout:BaseLayout,
    component:Termscondition
  },
  {
    path: "/refundpolicy",
    layout:BaseLayout,
    component:Refundpolicy
  },
  {
    path: "/privacypolicy",
    layout:BaseLayout,
    component:Privacypolcy
  },
  {
    path: "/contact",
    layout:BaseLayout,
    component:ContactContainer
  },
  {
    path: "/checkout",
    layout:BaseLayout,
    component:CheckOutContainer
  },
  {
    path: "/blog",
    layout:BaseLayout,
    component:BlogContainer
  },
  {
    path: "/blog-details",
    layout:BaseLayout,
    component:BlogdetailsContainer
  },
];

export default routes;
