import React, { Component } from "react";
import LoginRegister from "../../components/LoginRegisterModal";
import Auth from "../../modules/Auth";
import { Button } from "react-bootstrap";
import aboutimage1 from "../../assets/images/aboutiamge1.png";
import aboutimage2 from "../../assets/images/aboutimage2.png";

class AboutContainer extends Component {

  render() {
    return (
      <div className="container single_product_container">
        <div>
          <div className="row">
            <div className="col">
              <div className="breadcrumbs d-flex flex-row align-items-center">
                <ul>
                  <li>
                    <a href="/">Home</a>
                  </li>
                  <li>
                    <a href="#">
                      <i className="fa fa-angle-right" aria-hidden="true"></i>
                      About
                    </a>
                  </li>
                </ul>
              </div>
            </div>
          </div>
          <h4 className="text-center" style={{ color: "red" }}>"Just Win Liquidation: Your one-stop shop for unbeatable deals and incredible finds!"</h4>
          <div style={{ display: "flex", justifyContent: "center", paddingTop: "40px" }}>
            <button className="text-center text-white" style={{ backgroundColor: "#3E373F", width: "300px", height: "70px", borderRadius: "50px", fontSize: "36px" }}><b>OUR STORY</b></button>
          </div>
          <div style={{ marginTop: "40px" }}>
            <p className="text-center" style={{ color: "red" }}>
              Just Win Liquidation is a family-owned business that began its journey in Utah when the family decided to generate additional income by selling items online. As time passed, they had to relocate to Coldwater, Michigan, due to work opportunities. They continued their online sales, expanding their reach to platforms like eBay and Mercari
            </p>
          </div>
          <div style={{ marginTop: "30px" }}>
            <p className="text-center" style={{ color: "black" }}>
              What sets them apart in the industry is their commitment to treating customers the way they would want to be treated themselves. They go above and beyond, providing exceptional customer service. Their dedication to offering excellent products at the best prices led to earning outstanding ratings on various platforms, receiving badges and rewards as a result.
              In January, an incredible opportunity arose when they were presented with the chance to open their first physical store in Downtown Coldwater. It was a challenge and a significant step forward for the family. Despite having experience in opening businesses in their home country of Ecuador, opening a store in the United States was uncharted territory. However, they fearlessly embraced the challenge and embarked on this exciting adventure.
            </p>
          </div>
          <div className="d-flex ">
            <div style={{ marginRight: "10px" }} >
              <img src={aboutimage1} />

            </div>
            <div style={{ marginLeft: "10px" }} >
              <img src={aboutimage2} />
            </div>
          </div>
          <div style={{ marginTop: "30px" }}>
            <p className="text-center" style={{ color: "black" }}>
              Since the grand opening on February 24, 2023, Just Win Liquidation has been a business that continues to grow steadily. They remain committed to supporting the community and have actively joined the Coldwater Chamber of Commerce. Moreover, they have contributed to events by offering prizes and consistently surprise their customers with new promotions and gifts, depending on the occasion.
              Just Win Liquidation is dedicated to creating an exceptional shopping experience for all its customers. With their constant site changes and renovations, they ensure a fresh and vibrant environment. Each week, they introduce a wide range of high-quality products and unbeatable offers.
              As a business that values community engagement and growth, Just Win Liquidation strives to go above and beyond to support its customers and make a positive impact in Coldwater
            </p>
          </div >
          <div style={{ display: "flex", justifyContent: "center", paddingTop: "40px" }}>
            <button className="text-center text-white" style={{ backgroundColor: "red", width: "400px", height: "70px", borderRadius: "50px", fontSize: "24px", borderColor: "red" }}>GO TO THE AUCTIONS SECTION </button>
          </div>

        </div>
      </div>
    );
  }
}

export default AboutContainer;
