import React from "react";
import Modal from "react-modal";
import modalimage from "../../assets/images/Group 8.png"

import "./modal.css";

export default ({ isOpen, onRequestClose, name }) => (
    <Modal
        isOpen={isOpen}
        onRequestClose={onRequestClose}
        contentLabel="Modal"
        className={{
            base: "modal-base",
            afterOpen: "modal-base_after-open",
            beforeClose: "modal-base_before-close"
        }}
        overlayClassName={{
            base: "overlay-base",
            afterOpen: "overlay-base_after-open",
            beforeClose: "overlay-base_before-close"
        }}
        shouldCloseOnOverlayClick={true}
        closeTimeoutMS={2000}
    >
        <div className="row">
            <div className="col-lg-6">
                <img src={modalimage} style={{padding:"5%", width:"100%"}}/>
            </div>
            <div className="col-lg-6" style={{padding:"5%"}}>
                <h4 className="text-left text-white"><b>SIGN UP FOR</b></h4>
                <h4 className="text-left" style={{color:"red"}}><b>NEWSLETTER</b></h4><br/>
                <p className="text-left" style={{marginLeft:"10px", marginRight:"10px"}}>Signup for our newsletter to get notified about sales and new products. Add any text here or remove it.</p>
            </div>
            
        </div>

        <div className="row">
            <div className="col-lg-8">
                <input placeholder="Email Address" style={{paddingLeft:"20px", backgroundColor:"white", height:"50px", width:"100%", borderBottomLeftRadius:"30px", borderTopLeftRadius:"30px", marginLeft:"30px"}}/>
            </div>
            <div className="col-lg-4">
                <button className="text-left" style={{paddingLeft:"20px", backgroundColor:"red", height:"50px", width:"80%", borderBottomRightRadius:"30px", borderTopRightRadius:"30px",}}>Signup</button>
            </div>
        </div>
        <div className=" d-flex text-center" style={{justifyContent:"center"}} >
            <input   type="checkbox"></input>
            <h6  style={{marginLeft:"5px", marginTop:"0.5rem", color:"#6c757d"}}>I accept to receive  SMS, Emails and News notifications</h6>
        </div>
        <div className=" text-center">
            <p style={{color:"#6c757d"}}>Your email is safe with us, we don't spam.</p>
        </div>
        
    </Modal>
);
