import React, { Component } from "react";
import LoginRegister from "../LoginRegisterModal";
import { Link } from "react-router-dom";
import Auth from "../../modules/Auth";
import { AiOutlineMail } from "react-icons/ai";
import Modal from "./Modal";
import { TiMail } from "react-icons/ti";

class TopNavBar extends Component {
  constructor(props) {
    super(props);
    this.state = {
      modalShow: false,
      login: true,
      isOpen: false
    };
  }
  showHideModal = () => {
    this.setState({ modalShow: false });
  };

  loginClicked = () => {
    this.setState({ modalShow: true, login: true });
  };
  registerClicked = () => {
    this.setState({ modalShow: true, login: false });
  };

  logout = () => {
    Auth.logout();
    window.location.reload();
  };

  handleOpenModal = () => {
    this.setState({ isOpen: !this.state.isOpen });
  };


  render() {
    return (
      <div className={`top_nav ${this.props.className}`}>
        <div className="container">
          <div className="row">
            <div className="col-md-6">
              <div className="top_nav_left">
                HERE TODAY! GONE TOMORROW? <Link to="/"><b style={{ textDecoration: 'underline' }}>ShopNow</b></Link>
              </div>
            </div>
            <div className="col-md-6 text-right">
              <div className="top_nav_right">
                <ul className="top_nav_menu">

                  <li className="currency">
                    <Link to="/blog">Blog</Link>
                  </li>

                  <li className="currency">
                    <Link to="/contact">Contact</Link>
                  </li>

                  <li className="currency">
                    <Link to="/about">About</Link>

                  </li>

                  <li className="currency">
                    <Link to="/faqs">
                      FAQs
                    </Link>
                  </li>

                  <li className="currency" onClick={this.handleOpenModal}>
                    <a href="#">
                      <TiMail size={18} style={{marginTop:"-5px"}}/>
                      Newsletter
                    </a>
                  </li>
                  {this.state.isOpen && (
                    <Modal
                      isOpen={this.state.isOpen}
                      onRequestClose={this.handleOpenModal}
                    />
                  )}
                </ul>
              </div>
            </div>
          </div>
        </div>
        {this.state.modalShow ? (
          <LoginRegister
            show={this.state.modalShow}
            login={this.state.login}
            registerClicked={() => this.registerClicked()}
            loginClicked={() => this.loginClicked()}
            onHide={() => this.showHideModal()}
          />
        ) : null}
      </div>
    );
  }
}

export default TopNavBar;
