import React, { Component } from "react";
import { login } from "../../ServerRequest";
import API from "../../axios/API";
import Auth from "../../modules/Auth";
import HomeBanner from "../../components/HomeBanner";
import CategoryBanner from "../../components/CategoryBanner/CategoryBanner";
import NewArrivals from "../../components/Products/NewArrivals";
import BestSeller from "../../components/Products/BestSeller";
import Benefit from "../../components/Benefit";
import Advertisement from "../../components/Advertisement";
import LoginRegister from "../../components/LoginRegisterModal";
import AuctionArrival from "../../components/Products/AuctionArrivals";

// Swiper

class Home extends Component {
  constructor(props) {
    super(props);
    this.state = {
      data: null,
      modalShow: false,
      login: true
    };
    this.addToBag = this.addToBag.bind(this);
  }



  componentDidMount() {
    if (!this.props.products) {
      this.props.getAllProducts();
    }
  }

  showHideModal = () => {
    this.setState({ modalShow: false });
  };

  loginClicked = () => {
    this.setState({ modalShow: true, login: true });
  };
  registerClicked = () => {
    this.setState({ modalShow: true, login: false });
  };

  addToBag = params => {
    alert("this is")

    if (
      Auth.getUserDetails() !== undefined &&
      Auth.getUserDetails() !== null &&
      Auth.getToken() !== undefined
    ) {
      let cart = this.props.postCart(params);
      cart.then(res => {
        console.log(res);
      });
    } else {
      this.setState({ modalShow: true });
    }
  };

  render() {
    const { products, departments } = this.props;
    return (
      <div>
        <HomeBanner />
        <div class="input-group " style={{ marginTop: "25px" }}>
          <div class="input-group-prepend" style={{ marginLeft: "5%", marginRight: "" }}>
            <span class="input-group-text" style={{ backgroundColor: "red" }}></span>
          </div>
          <p style={{ color: "red", fontSize: "20px", marginLeft: "20px", marginBottom: "0px", marginTop: '5px' }}>Today's</p>
        </div>
        <div className="d-flex">
          <p style={{ color: "red", paddingLeft: "50px", marginTop: "auto", fontSize: "36px" }}>Auctions</p>
          <Advertisement />
        </div>

        {products ? (
          <AuctionArrival
            products={products}
            departments={departments}
            addToBag={this.addToBag}
          />
        ) : null}
        <div style={{ display: "flex", justifyContent: "center", paddingTop: "40px" }}>
          <button className="text-center text-white" style={{ backgroundColor: "#3E373F", width: "300px", height: "70px", borderRadius: "50px", fontSize: "16px", borderColor: "#3E373F" }}>View All Auctions </button>
        </div>
        <p style={{ color: "red", paddingLeft: "50px", fontSize:"36px" }}>Upcoming Auctions</p>
        {products ? (
          <NewArrivals
            products={products}
            departments={departments}
            addToBag={this.addToBag}
          />
        ) : null}
        <div style={{ display: "flex", justifyContent: "center", paddingTop: "40px" }}>
          <button className="text-center text-white" style={{ backgroundColor: "#3E373F", width: "350px", height: "70px", borderRadius: "50px", fontSize: "16px", borderColor: "#3E373F" }}>View All Upcoming Auctions </button>
        </div>
        <p style={{ color: "red", paddingLeft: "50px", fontSize:"36px" }}>Buy It Now!</p>
        {products ? (
          <NewArrivals
            products={products}
            departments={departments}
            addToBag={this.addToBag}
          />
        ) : null}

        <div style={{ display: "flex", justifyContent: "center", paddingTop: "40px", marginBottom: "40px" }}>
          <button className="text-center text-white" style={{ backgroundColor: "red", width: "400px", height: "70px", borderRadius: "50px", fontSize: "24px", borderColor: "red" }}>GO TO THE AUCTIONS SECTION </button>
        </div>
        {/* <Benefit /> */}

        {/* {products ? (
          <BestSeller
            products={products}
            departments={departments}
            addToBag={this.addToBag}
          />
        ) : null} */}
        <LoginRegister
          show={this.state.modalShow}
          login={this.state.login}
          registerClicked={() => this.registerClicked()}
          loginClicked={() => this.loginClicked()}
          onHide={() => this.showHideModal()}
        />
      </div>
    );
  }
}

export default Home;
