 import React, { Component } from "react";
 import SingleProduct from "./SingleProduct";
 import Heading from "../Heading";
 import PropTypes from "prop-types";
 import Banner1 from "../../assets/images/banner_1.jpg";
 import AuctionProduct from "./AuctionProduct";
 
 
 class AuctionArrivals extends Component {
   constructor(props) {
     super(props);
     this.state = {
       products: this.props.products,
       productsBAK: this.props.products,
       departments: this.props.departments
     };
   }
 
   optionClicked(option) {
     let FilterList = this.state.productsBAK.filter(
       item => item.department === option
     );
     if (FilterList.length > 0) {
       this.setState({ products: FilterList });
     } else {
       this.setState({ products: this.state.productsBAK });
     }
     this.setState({ selectedOption: option });
   }
 
   render() {
     const { products, departments } = this.state;
 
     console.log(products)
     return (
       <div className="new_arrivals" data-aos="fade-up">
           <div className="banner">
             <div className="row">
 
               <swiper-container class="mySwiper" pagination="true" pagination-clickable="true" space-between="30"
                 slides-per-view="3">
                 {products &&
                   products.map((item, index) => {
                     return (
                       <div
                         className="col-lg-3 col-sm-6"
                         style={{
                           maxWidth: '300px'
                         }}
                         key={index}
                         data-aos="zoom-in"
                       >
                           {/* <SingleProduct
                             productItem={item}
                             addToBag={this.props.addToBag}
                           /> */}
                             <AuctionProduct
                             productItem={item}
                             addToBag={this.props.addToBag}
                           />
                       </div>
                     );
                   })}
                 <swiper-slide></swiper-slide>
                 <swiper-slide></swiper-slide>
                 <swiper-slide></swiper-slide>
                 <swiper-slide></swiper-slide>
                 <swiper-slide></swiper-slide>
                 <swiper-slide></swiper-slide>
                 <swiper-slide></swiper-slide>
                 <swiper-slide></swiper-slide>
               </swiper-container>
 
 
             </div>
 
           </div>
           {/* <div className="row">
             {products &&
               products.slice(0, 8).map((item, index) => {
                 return (
                   <div
                     className="col-lg-3 col-sm-6"
                     key={index}
                     data-aos="zoom-in"
                   >
                     <SingleProduct
                       productItem={item}
                       addToBag={this.props.addToBag}
                     />
                   </div>
                 );
               })}
           </div> */}
         </div>
     );
   }
 }
 
 AuctionArrivals.propTypes = {
   addToCart: PropTypes.func
 };
 
 export default AuctionArrivals;
 