import React, { Component } from "react";
import LoginRegister from "../../components/LoginRegisterModal";
import Auth from "../../modules/Auth";
import { size } from "../../modules/mediaQuery";
import NewArrivals from "../../components/Products/NewArrivals";
import baseUrl from "../../config";

class Blogdetails extends Component {
  constructor(props) {
    super(props);
    this.state = {
      color: "",
      size: "",
      pic: "",
      selectedSize: "",
      id: "",
      quantity: 1,
      modalShow: false,
      login: true,
    };
  }
  componentDidMount() {
    this.props.getProduct(this.props.location.pathname.split("/").slice(-1)[0]);
    this.props.getVariantsByProductId(
      this.props.location.pathname.split("/").slice(-1)[0]
    );
  }

  showHideModal = () => {
    this.setState({ modalShow: false });
  };

  loginClicked = () => {
    this.setState({ modalShow: true, login: true });
  };
  registerClicked = () => {
    this.setState({ modalShow: true, login: false });
  };

  handleThumbnailClick = (item) => {
    this.setState({
      color: item.color,
      size: item.size,
      pic: item.imagePath,
      selectedSize: "",
      id: item._id,
      cartItem: null,
    });
  };

  onAddClicked = () => {
    this.setState({ quantity: this.state.quantity + 1 });
    this.props.postCart(
      this.state.id || this.props.location.pathname.split("/").slice(-1)[0],
      true,
      false
    );
  };
  onRemoveClicked = () => {
    this.props.postCart(
      this.state.id || this.props.location.pathname.split("/").slice(-1)[0],
      false,
      true
    );

    if (this.state.quantity > 1) {
      this.setState({ quantity: this.state.quantity - 1 });
    }
  };

  addToBag = () => {
    if (
      Auth.getUserDetails() !== undefined &&
      Auth.getUserDetails() !== null &&
      Auth.getToken() !== undefined
    ) {
      this.props
        .postCart(
          this.state.id || this.props.location.pathname.split("/").slice(-1)[0]
        )
        .then((res) => {
          console.log(res);
        });
    } else {
      this.setState({ modalShow: true });
    }
  };

  productInCart = () => {
    let available = false;
    return available;
  };

  render() {
    const { products, departments } = this.props;
    console.log(this.props);
    return (
      <div>
        {this.props.product && (
          <div>
            <div className="col-lg-12">
              <div className="single_product_image" style={{ paddingTop: '10%' }}>

                <div
                  className="single_product_image_background"
                  style={{
                    // backgroundSize:"contain",
                    backgroundImage: `url(${baseUrl}/${this.state.pic || this.props.product.imagePath
                      })`,
                  }}
                />
                <div className="container">
                  <p className="text-white" style={{ fontSize: "48px", }}>
                    <b>{this.props.product.title}</b>
                  </p>
                </div>
              </div>

            </div>
            <div className="container">
              <p style={{ color: "black" }}>{this.props.product.description}</p>
            </div>
            <div className="container" >
              <div className="col-lg-12 " >
                <div className="single_product" >
                  <table>
                    <tr>
                      {this.props.variants &&
                        this.props.variants
                          .slice(0, 4)
                          .map((item, index) => (
                            <th
                              key={index}
                              onClick={() =>
                                this.handleThumbnailClick(item)
                              }
                            >
                              <img
                                src={item.imagePath}
                                alt="image"
                                className="img-fluid"
                              />
                            </th>
                          ))}
                    </tr>
                  </table>
                </div>
              </div>
              <div className="container">
                <p style={{ color: "black" }}>{this.props.product.description}</p>
              </div>
            </div>
 
            <div style={{ display: "flex", justifyContent: "center", paddingTop: "40px" }}>
              <button className="text-center text-white" style={{ backgroundColor: "red", width: "400px", height: "70px", borderRadius: "50px", fontSize: "24px", borderColor: "red" }}>GO TO THE AUCTIONS SECTION </button>
            </div>
          </div>
        )}

        <LoginRegister
          show={this.state.modalShow}
          login={this.state.login}
          registerClicked={() => this.registerClicked()}
          loginClicked={() => this.loginClicked()}
          onHide={() => this.showHideModal()}
        />
      </div>
    );
  }
}

export default Blogdetails;
